"use client";

import React, { useState, useEffect } from 'react';
import { VehicleData, fetchExcelData, filterByFC, filterByTransportadora, getStatistics } from '@/lib/excelData';
import VehicleTrackingTable from '@/components/VehicleTrackingTable';
import StatisticsCards from '@/components/StatisticsCards';

const transportadoras = [
  'INNOVATION',
  'GARBERG', 
  'FULL FLEX',
  'PACHECO',
  'FRATELLI',
  'SANPOR',
  'PRC TRANSPORTES'
];

const fcs = ['GR8', 'GR9', 'XC9'];

export default function Home() {
  const [data, setData] = useState<VehicleData[]>([]);
  const [filteredData, setFilteredData] = useState<VehicleData[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedFC, setSelectedFC] = useState<string>('');
  const [selectedTransportadora, setSelectedTransportadora] = useState<string>('');
  const [lastUpdate, setLastUpdate] = useState<Date | null>(null);

  const loadData = async () => {
    setLoading(true);
    setError(null);
    
    try {
      const result = await fetchExcelData();
      setData(result);
      setLastUpdate(new Date());
    } catch (err) {
      setError('Erro ao carregar dados da planilha. Exibindo dados de exemplo.');
      console.error('Erro:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData();
  }, []);

  useEffect(() => {
    let filtered = data;
    
    if (selectedFC) {
      filtered = filterByFC(filtered, selectedFC);
    }
    
    if (selectedTransportadora) {
      filtered = filterByTransportadora(filtered, selectedTransportadora);
    }
    
    setFilteredData(filtered);
  }, [data, selectedFC, selectedTransportadora]);

  const statistics = getStatistics(filteredData);

  const handleRefresh = () => {
    loadData();
  };

  const clearFilters = () => {
    setSelectedFC('');
    setSelectedTransportadora('');
  };

  return (
    <div className="space-y-8">
      {/* Cabeçalho com informações */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
          <div>
            <h2 className="text-xl font-semibold text-gray-900 mb-2">
              Rastreamento de Veículos Amazon
            </h2>
            <p className="text-gray-600">
              Monitoramento em tempo real das rotas de transferência com origem em TZX
            </p>
            {lastUpdate && (
              <p className="text-sm text-gray-500 mt-2">
                Última atualização: {lastUpdate.toLocaleString('pt-BR')}
              </p>
            )}
          </div>
          <div className="mt-4 lg:mt-0">
            <button
              onClick={handleRefresh}
              disabled={loading}
              className="bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white px-6 py-2 rounded-lg font-medium transition-colors"
            >
              {loading ? 'Atualizando...' : 'Atualizar Dados'}
            </button>
          </div>
        </div>
      </div>

      {/* Estatísticas */}
      <StatisticsCards {...statistics} />

      {/* Filtros */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
          <div className="flex flex-col sm:flex-row gap-4">
            <div>
              <label htmlFor="fc-filter" className="block text-sm font-medium text-gray-700 mb-1">
                Filtrar por FC:
              </label>
              <select
                id="fc-filter"
                value={selectedFC}
                onChange={(e) => setSelectedFC(e.target.value)}
                className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="">Todos os FC</option>
                {fcs.map(fc => (
                  <option key={fc} value={fc}>{fc}</option>
                ))}
              </select>
            </div>

            <div>
              <label htmlFor="transportadora-filter" className="block text-sm font-medium text-gray-700 mb-1">
                Filtrar por Transportadora:
              </label>
              <select
                id="transportadora-filter"
                value={selectedTransportadora}
                onChange={(e) => setSelectedTransportadora(e.target.value)}
                className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="">Todas as Transportadoras</option>
                {transportadoras.map(transportadora => (
                  <option key={transportadora} value={transportadora}>{transportadora}</option>
                ))}
              </select>
            </div>
          </div>

          <div className="flex items-end gap-2">
            {(selectedFC || selectedTransportadora) && (
              <button
                onClick={clearFilters}
                className="text-sm text-gray-600 hover:text-gray-800 underline"
              >
                Limpar Filtros
              </button>
            )}
            <div className="text-sm text-gray-600">
              Exibindo {filteredData.length} de {data.length} registros
            </div>
          </div>
        </div>
      </div>

      {/* Informações das Transportadoras */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">
          Cobertura das Transportadoras
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {transportadoras.map(transportadora => (
            <div key={transportadora} className="border rounded-lg p-4">
              <h4 className="font-medium text-gray-900 mb-2">{transportadora}</h4>
              <div className="text-sm text-gray-600">
                <p>Atende FC:</p>
                <div className="flex gap-2 mt-1">
                  {(transportadora === 'INNOVATION' || transportadora === 'SANPOR') ? (
                    <>
                      <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">GR8</span>
                      <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">GR9</span>
                      <span className="bg-red-100 text-red-800 px-2 py-1 rounded text-xs">XC9 ✗</span>
                    </>
                  ) : (
                    <>
                      <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">GR8</span>
                      <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">GR9</span>
                      <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">XC9</span>
                    </>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Mensagem de erro */}
      {error && (
        <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
          <div className="flex">
            <div className="flex-shrink-0">
              <div className="w-5 h-5 bg-yellow-400 rounded-full"></div>
            </div>
            <div className="ml-3">
              <p className="text-sm text-yellow-800">{error}</p>
            </div>
          </div>
        </div>
      )}

      {/* Tabela de dados */}
      <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-semibold text-gray-900">
            Dados da Planilha Excel
          </h3>
          <p className="text-sm text-gray-600 mt-1">
            Informações sincronizadas da planilha "TRANSFERÊNCIA PCP - 2025"
          </p>
        </div>
        
        <VehicleTrackingTable data={filteredData} loading={loading} />
      </div>
    </div>
  );
}
