"use client";

import React from 'react';
import { VehicleData } from '@/lib/excelData';

interface VehicleTrackingTableProps {
  data: VehicleData[];
  loading: boolean;
}

const VehicleTrackingTable: React.FC<VehicleTrackingTableProps> = ({ data, loading }) => {
  if (loading) {
    return (
      <div className="flex justify-center items-center py-12">
        <div className="text-lg text-gray-600">Carregando dados da planilha...</div>
      </div>
    );
  }

  if (!data.length) {
    return (
      <div className="text-center py-12">
        <div className="text-lg text-gray-600 mb-2">Nenhum dado encontrado</div>
        <div className="text-sm text-gray-500">Verifique os filtros aplicados</div>
      </div>
    );
  }

  return (
    <div className="overflow-x-auto bg-white rounded-lg shadow-sm border">
      <table className="min-w-full divide-y divide-gray-200">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              ISA
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Data Carga
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Rota
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Transportadora
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Tipo Veículo
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Motorista
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Placa
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Status
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Pallets
            </th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Finalizado
            </th>
          </tr>
        </thead>
        <tbody className="bg-white divide-y divide-gray-200">
          {data.map((item, index) => (
            <tr key={item.idViagem} className={index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
              <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                {item.isa}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-600">
                {item.dataCarga}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-900">
                <span className="font-medium">{item.rota}</span>
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-900">
                <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full text-xs font-medium">
                  {item.transportadora}
                </span>
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-600">
                {item.tipoVeiculo}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-600">
                {item.motorista}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm font-mono text-gray-900">
                {item.placa}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm">
                <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                  item.status === 'Finalizado' 
                    ? 'bg-green-100 text-green-800'
                    : item.status === 'Em Trânsito'
                    ? 'bg-yellow-100 text-yellow-800'
                    : item.status === 'Carregando'
                    ? 'bg-blue-100 text-blue-800'
                    : 'bg-gray-100 text-gray-800'
                }`}>
                  {item.status}
                </span>
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-600 text-center">
                {item.pallets}
              </td>
              <td className="px-4 py-3 whitespace-nowrap text-sm">
                <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                  item.finalizado === 'Sim' 
                    ? 'bg-green-100 text-green-800'
                    : 'bg-red-100 text-red-800'
                }`}>
                  {item.finalizado}
                </span>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VehicleTrackingTable;
